<template>
  <div id="over_all_view_box">
    <!-- search -->
    <div class="txal mg-10">
      <el-autocomplete
        popper-class="my-autocomplete"
        v-model="inputName"
        :fetch-suggestions="querySearch"
        placeholder=""
        @select="handleSelect">
        <i
          class="el-icon-edit el-input__icon"
          slot="suffix"
          @click="handleIconClick">
        </i>
        <template slot-scope="{ item }">
          <div class="name">{{ item.value }}</div>
          <span class="addr">{{ item.address }}</span>
        </template>
      </el-autocomplete>
    </div>
    <!-- connent -->
    <div class="fsa mg-10">
      <!--one  -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/sale/view"></router-link>
          <router-link class="rout_lin txal" to="/sale/record/day"></router-link>
          <router-link class="rout_lin txal" to="/sale/record/class"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
        </div>
      </div>
      <!-- two -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/sale/data/trend"></router-link>
          <router-link class="rout_lin txal" to="/sale/data/proportion"></router-link>
          <router-link class="rout_lin txal" to="/sale/data/sale"></router-link>
          <router-link class="rout_lin txal" to="/sale/data/one"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
        </div>
      </div>
    </div>
    <!-- connent 2-->
    <div class="fsa mg-10">
      <!-- three -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/shopping/overview"></router-link>
          <router-link class="rout_lin txal" to="/shopping/store/chart"></router-link>
          <router-link class="rout_lin txal" to="/shopping/store/del"></router-link>
          <router-link class="rout_lin txal" to="/shopping/store/change"></router-link>
          <router-link class="rout_lin txal" to="/shopping/store/history"></router-link>
        </div>
      </div>
      <!-- fore -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/member/allview"></router-link>
          <router-link class="rout_lin txal" to="/member/table/massge"></router-link>
          <router-link class="rout_lin txal" to="/member/table/once"></router-link>
          <router-link class="rout_lin txal" to="/member/table/scor"></router-link>
        </div>
      </div>
    </div>
    <!-- connent 3-->
    <div class="fsa mg-10">
      <!-- five -->
      <div class="" style="width:40%;border:1px solid #666;height: 212px;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/staff/staffCen/create"></router-link>
          <router-link class="rout_lin txal" to="/staff/staffCen/succ"></router-link>
        </div>
      </div>
      <!-- six -->
      <div class="" style="width:40%;border:1px solid #666;">
        <h6 class="txal mg-10"></h6>
        <div class="lh-58">
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
          <router-link class="rout_lin txal" to="/"></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    data() {
      return {
        restaurants: [],
        inputName: ''
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          // console.log(restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()))
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) !== -1);
        };
      },
      loadAll() {
        return [
          { "value": "", "router": "/sale/view" },
          { "value": "", "router": "/sale/record/day" },
          { "value": "", "router": "/sale/record/class" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/sale/data/trend" },
          { "value": "", "router": "/sale/data/proportion" },
          { "value": "", "router": "/sale/data/sale" },
          { "value": "", "router": "/sale/data/one" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/shopping/overview" },
          { "value": "", "router": "/shopping/store/chart" },
          { "value": "", "router": "/shopping/store/del" },
          { "value": "", "router": "/shopping/store/change" },
          { "value": "", "router": "/shopping/store/history" },
          { "value": "", "router": "/member/allview" },
          { "value": "", "router": "/member/table/massge" },
          { "value": "", "router": "/member/table/once" },
          { "value": "", "router": "/member/table/scor" },
          { "value": "", "router": "/staff/staffCen/create" },
          { "value": "", "router": "/staff/staffCen/succ" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
          { "value": "", "router": "/" },
        ];
      },
      handleSelect(item) {
        // console.log(item.router);
        var rout=item.router;
        this.$router.push(rout)
      },
      handleIconClick(ev) {
        console.log(ev);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
}
</script>

<style lang="less" scoped>
.my-autocomplete {
  li {
    line-height: normal;
    padding: 7px;

    .name {
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .addr {
      font-size: 12px;
      color: #b4b4b4;
    }

    .highlighted .addr {
      color: #ddd;
    }
  }
}
.rout_lin{
  width: 32%;
  display: inline-block;
}
</style>




